.main {
  display: flex;
  flex-direction: column;
  width: 660px;
  margin: 40px 0px 0px 0px;
}
.main_item_1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.wrapper_1 {
  h3 {
    font-family: "Lexend";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    color: var(--color_gray);
    margin-bottom: 0px;
  }
  p {
    @include main_p;
    margin-top: 8px;
  }
}
.wrapper_2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #f3f5f9;
  border-radius: 10px;
  width: 275px;
  height: 50%;
  margin-top: 20px;
  p {
    @include main_p;
    opacity: 0.5;
    margin: 12px 38px 12px 16px;
  }
  img {
    padding-right: 19px;
  }
}
.main_item_2 {
  display: flex;
  flex-direction: column;
  margin-top: 64px;
  h2 {
    font-family: "Lexend";
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 130%;
    color: var(--color_gray);
    margin-bottom: -10px;
  }
  p {
    @include main_p;
  }
  h1 {
    font-size: 50px;
    color: #1b224a;
    margin-top: 16px;
    margin-bottom: -10px;
    span {
      color: blue;
    }
  }
  .wrapper_3 {
    display: flex;
    flex-direction: row;
  }
  .wrapper_3_item_1 {
    background: #1c1d21;
    @include button;
    p {
      color: var(--color_white);
    }
  }
  .wrapper_3_item_2 {
    background: #f3f5f9;
    margin-left: 12px;
    @include button;
    p {
      color: var(--color_gray);
    }
  }
}

.main_item_3 {
  display: flex;
  flex-direction: column;

  .wrapper_4 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    h2 {
      font-family: "Lexend";
      font-style: normal;
      font-weight: 600;
      font-size: 26px;
      line-height: 130%;
      color: var(--color_gray);
    }
    p {
      font-weight: 400;
      color: var(--color_gray);
      font-size: 16px;
      margin-bottom: 45px;
    }
  }
  .wrapper_5 {
    table {
      width: 660px;
      td {
        border-bottom: 1px solid #efeff5;
        padding-bottom: 24px;
      }
      #green {
        color: #34c759;
      }
      #red {
        color: #ff6b6b;
      }
      h4 {
        font-family: "Lexend";
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 160%;
        color: var(--color_gray);
        margin-bottom: -20px;
      }
      p {
        @include main_p;
      }
    }
  }
  .opacity {
    color: #687d8c;
    opacity: 0.6;
  }
}
